@use "./variables" as *;
.todones-container{
    padding:10px;
    .todones-item{
        padding:5px 10px;
        background:$inputBg;
        margin:10px 0;
        border-radius:50px;
        width:300px;
        display:flex;
        align-items:center;
        justify-content: space-between;
        p{
            text-decoration: line-through;
            opacity:0.5;
        }
    }
}
.todos-container{
    padding:10px;
    .todo-form{
        .input-item{
            input{
                padding: 5px 10px;
                margin-right:10px;
                background-color: $inputBg;
                border-radius: 50px;
                width:300px;
                font-size:1.2em;
                color:$mainTxt;
                border: none;

            }
        }
    }
    .todos-item{
        padding:5px 10px;
        background:$inputBg;
        margin:10px 0;
        border-radius:50px;
        width:300px;
        display:flex;
        align-items:center;
        justify-content: space-between;
        
    }
}